import styled from "styled-components";

export const HeroContainer = styled.div`
    background: #57c6d1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0px;
    position: static;
    z-index: 1;
    }
`



export const HeroContent = styled.div` 
    z-index: 3; 
    width: 100% ; 
    max-width: 1100px;
    position: static;
    padding: 8px 24px;
    margin: 80px 0px;
    display: flex;
    flex-direction: column;
    align-items: center ; 
`
export const HeroH1 = styled.h1` 
    color: #fff;
    font-size: 48px;
    text-align: center;

    @media screen and (max-width: 768px) {
        font-size: 40px;
    }

    @media screen and (max-width: 480px) {
        font-size: 32px;
    }

`

export const HeroP = styled.p` 
    margin-top: 24px;
    color: #fff;
    font-size: 24px ; 
    text-align: center ; 
    max-width: 600px ; 

    
    @media screen and (max-width: 768px) {
        font-size: 24px;
    }

    @media screen and (max-width: 480px) {
        font-size: 18px;
    }
`

export const HeroBtnWrapper = styled.div`
    margin-top: 32px ; 
    display: flex;
    flex-direction: column ; 
    align-items: center ; 
`


export const HeroRow = styled.div` 
    display: grid; 
    grid-auto-columns: minmax(auto, 1fr);
    align-items: top;
    grid-template-areas:  'col1 col2' };

    @media screen and (max-width: 768px){
        grid-template-areas: 'col2' 'col1';
    }
`


export const HeroNewsItem = styled.div`
    margin-left: 4px;
    color: #000000;
    font-size: 16px;
    // line-height: 16px;
    margin-bottom: 16px;
    display: flex;  
    justify-content: start;
`
export const HeroNewsItemDate = styled.div`
    width: 20%;
    font-size: 16px ; 
    margin-right: 20px ; 

    @media screen and (max-width: 768px) {
        font-size: 24px;
    }

    @media screen and (max-width: 480px) {
        font-size: 18px;
    }
`

export const HeroNewsItemContent = styled.div`
    width: 80%; 
    color: #000000;
    font-size: 16px ; 

    @media screen and (max-width: 768px) {
        font-size: 24px;
    }

    @media screen and (max-width: 480px) {
        font-size: 18px;
    }
`



export const HeroColumn1 = styled.div` 
    margin-bottom: 15px;
    margin-left: 12px;
    margin-top: 60px;
    padding: 10px 15px;
    padding: 0 15px;
    grid-area: col1;
    align-items:left;
    display: flex;
    flex-direction:column;
    justify-content: space-between;
`


export const HeroColumn2 = styled.div` 
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area: col2;
    display: flex;
    align-items:center;
    flex-direction:column;
`

export const TextWrapper = styled.div`
    max-width: 540px ; 
    padding-top: 0;
    padding-bottom: 0;
`
export const HeroImgWrap = styled.div` 
    max-width: 555px;
    height: 100%;
`

export const Img = styled.img`
  width: 100%;
  margin-top: 0;
  margin-right: 0;
  margin-left: 10px;
  padding-right: 0;
`;

export const HeroDownloadsImg = styled.img`
    margin-top: 5px;
    margin-right: 0;
    margin-left: 0;
    padding-right: 0;
    margin-bottom: 10px;
`

export const HeroLink = styled.a`
  color: #fff;
  text-decoration: none;
  padding: 0.1rem 0rem;
  height: 100%;
  cursor: pointer;

  &:hover {
    filter: brightness(0.85);
  }

  &.active {
    color: #000
    border-bottom: 3px solid #01bf71;
  }
`;


export const HeroNewsTitle = styled.div` 
    margin-left: 0px;
    color: #000000;
    font-size: 16px;
    font-weight: bold;
    line-height: 16px;
    margin-bottom: 36px; 

    @media screen and (max-width: 768px) {
        font-size: 24px;
    }

    @media screen and (max-width: 480px) {
        font-size: 18px;
    }
`

export const HeroCitationTitle = styled.div` 
    margin-left: 0px;
    color: #000000;
    font-size: 16px;
    font-weight: bold;
    line-height: 16px;
    margin-bottom: 10px; 
    margin-top:36px;

    @media screen and (max-width: 768px) {
        font-size: 24px;
    }

    @media screen and (max-width: 480px) {
        font-size: 18px;
    }
`


export const HeroNewsBlock = styled.div`

`
export const HeroCitationBlock = styled.div`
    font-size: 14px;
    margin-bottom: 0px;
    margin-top: 0px;

`




export const HeroFollowBlock = styled.div`
@media screen and (max-width: 768px) {
    font-size: 24px;
}

@media screen and (max-width: 480px) {
    font-size: 18px;
}
`